<template>
  <div>
    <table class="table rwdTable">
      <thead>
        <th width="120">名稱</th>
        <th width="60">折扣百分比</th>
        <th width="60">到期日</th>
        <th width="60">狀態</th>
        <th width="60">編輯</th>
      </thead>
      <tbody>
        <tr v-for="(item) in coupons_menu" :key="item.id">
          <td scope="col" class="rwdTitle" data-title="名稱">{{item.title}}</td>
          <td scope="col" class="rwdTitle" data-title="折扣百分比">{{item.percent}} %</td>
          <td scope="col" class="rwdTitle" data-title="到期日">{{item.due_date|timeChanger}}</td>
          <td scope="col" class="rwdTitle" data-title="狀態">
            <span v-if="item.is_enabled" class="text-success">啟用</span>
            <span v-else>未啟用</span>
          </td>
          <td scope="col" class="rwdTitle" data-title="編輯">
            <div class="btn-group">
              <button class="btn btn-outline-primary btn-sm" @click="editHandler(item)">編輯</button>
              <button class="btn btn-outline-danger btn-sm" @click="delHandler(item)">刪除</button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import $ from 'jquery'

export default {
  data(){
    return{}
  },
  props:{
    coupons_menu:{
      type:Array,
      required:true
    }
  },
  methods:{
    editHandler(data){
      $('#addCouponModal').modal('show')
      this.$emit('changeHandler',data)
    },
    delHandler(data){
      $('#delCouponModal').modal('show')
      this.$emit('changeHandler',data)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/helper/_rwdTable";
</style>