<template>
  <div class="container">

    <div>
      <loading :active.sync="isLoading_status"></loading>
    </div>

    <div class="my-3 mx-auto">
      
      <div class="my-2 mx-auto">
        <ProductsList :products_menu="products_menu"  :status="status" @infoHandler="infoHandler" @addToCartHandler="addToCartHandler"/>
      </div>

      <div>
        <InfoModal :productInfo_menu ="productInfo_menu" @addToCartHandler="addToCartHandler"/>
      </div>

      <div class="my-2 mx-auto">
        <Pagination :pagination_menu="pagination_menu" @pageChange="getStoreList"/>
      </div>

      <div>
        <CartBtn/>
      </div>
    </div>

  </div>
</template>

<script>
import $ from 'jquery'
import Pagination from '@/components/Pagination'
import InfoModal from '@/components/Home/Store/Cutomer_Modal_ProductInfo'
import ProductsList from '@/components/Home/Store/Customer_ProductsList'
import CartBtn from '@/components/Home/Customer_CartButton'
export default {
  data(){
    return{
      status:{
        infoLoading:'',
        buyLoading:''
      }
    }
  },
  components:{
    Pagination,
    ProductsList,
    InfoModal,
    CartBtn
  },
  methods:{
    getStoreList(page = 1){
      this.$store.dispatch('init_store_list',page)
    },
    getProductInfo(id){
      this.$store.dispatch('init_product_info',id)
    },
    getCartData(){
      this.$store.dispatch('init_cart_data')
    },
    infoHandler(id){
      this.status.infoLoading = id
      this.getProductInfo(id)
      window.setTimeout(() => {
        this.status.infoLoading = ''
        $('#infoModal').modal('show')
        },500)
    },
    addToCartHandler(id,qty = 1){
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;
      const cart = {
        product_id: id,
        qty: qty
      }

      this.status.buyLoading = id

      this.$http.post(api, {data: cart}).then((response)=>{
        this.status.buyLoading = ''
        if(response.data.success){
          this.$bus.$emit('message:push',response.data.message,'success');
        }
        else{
          this.$bus.$emit('message:push',response.data.message,'danger');
        }

        $('#infoModal').modal('hide')
        this.getCartData()
      })      
    }
  },
  created(){
    this.getStoreList()
  },
  computed:{
    products_menu(){
      return this.$store.getters['STORE_LIST_MENU']
    },
    pagination_menu(){
      return this.$store.getters['STORE_PAGINATION_MENU']
    },
    productInfo_menu(){
      return this.$store.getters['PRODUCT_INFO_MENU']
    },
    isLoading_status(){
      return this.$store.getters['LOADING_STATUS']
    }
  }
}
</script>