<template>
  <div class="cart-div" @click="openCartModal">
    <button class="cart-btn btn-primary" type="button">
      <i class="fas fa-shopping-cart"></i>
    </button>
    <div class="text-light bg-danger cart-counter" v-if="cart_counter>0">
      {{cart_counter}}
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  data(){
    return{}
  },
  methods:{
    openCartModal(){
      $('#customerCartModal').modal('show')
    }
  },
  computed:{
    cart_counter(){
      let cart_data = this.$store.getters['CART_MENU']

      return cart_data.carts.length
    }
  }
}
</script>

<style lang="scss" scoped>
.cart-div{
  display: none;
}

@media only screen and (max-width:767px){
  .cart-div {
    display:block;
    position: fixed;
    right: 0;
    bottom: 18vh;
    width: 60px;
    height: 60px;
    line-height: 60px;
    text-align: center;
    margin: 30px;
    padding: 5px;
    z-index: 16;
    .cart-btn {
      outline-style: none;
      border-style: none;
      height: 60px;
      width: 60px;
      font-size: 30px;
      border-radius: 50%;
      color: white;
    }
    .cart-counter {
      position: absolute;
      left: 0vh;
      top: 0vh;
      width: 30px;
      height: 30px;
      line-height: 30px;
      font-size: 17px;
      font-weight: 800;
      text-align: center;
      border-radius: 50%;
      user-select: none;
      cursor: pointer;
    }
  }
}
</style>