<template>
  <nav>
    <ul class="pagination">
      <li class="page-item" >
        <a class="page-link normalStyle" :class="{'noneStyle': !pagination_menu.has_pre}" @click.prevent="pageChange(pagination_menu.current_page - 1)">
          <span aria-hidden="true">&laquo;</span>  
        </a>
      </li>

      <li class="page-item" v-for="page in pagination_menu.total_pages" :key="page">
        <a class="page-link normalStyle" :class="{'selStyle': pagination_menu.current_page == page}" @click.prevent="pageChange(page)">
          {{ page }}
        </a>
      </li>
        
      <li class="page-item" :class="{'noneStyle': !pagination_menu.has_next}">
        <a class="page-link normalStyle" @click.prevent="pageChange(pagination_menu.current_page + 1)">
          <span aria-hidden="true">&raquo;</span>
        </a>
      </li>

    </ul>
  </nav>     
</template>

<script>
export default {
    data(){
        return{
        }
    },
    props:{
        pagination_menu:{
            type:Object,
            required:true
        }
    },
    methods:{
        pageChange(item){
          if(this.pagination_menu.current_page !== Number(item)){
            this.$emit('pageChange',Number(item))
          }
        }
    },
}
</script>

<style lang="scss" scoped>
.normalStyle{
  color: #343a40;
  user-select: none;
  cursor: pointer;
}
.selStyle{
  border-color: #343a40;
  outline-color: #343a40;
  background-color: #343a40;
  color: #fff;
  user-select: none;
  cursor: default;
}
.noneStyle{
  display: none;
}
</style>
