<template>
  <div class="modal fade" id="delCouponModal" tabindex="-1" aria-hidden="true">
    <div class="modal-dialog">
      <div class="modal-content border-0">
        <div class="modal-header bg-danger text-white">
          <h5 class="modal-title">
            <span>刪除優惠券</span>
          </h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span class="text-light" aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          是否刪除 <strong class="text-danger">{{tempCoupon.title}}</strong>(刪除後將無法恢復)。
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">取消</button>
          <button type="button" class="btn btn-danger" @click="delData(tempCoupon.id)">確認</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  data(){
    return{}
  },
  props:{
    tempCoupon:{
      type:Object,
      required:true
    }
  },
  methods:{
    delData(id){
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${id}`;

      this.$http.delete(api).then((response) => {
          if(response.data.success){
            this.$bus.$emit('message:push',response.data.message,'success');
          }
          else{
            this.$bus.$emit('message:push',response.data.message,'danger');
          }

          this.$emit('completeUpdate')
          $('#delCouponModal').modal('hide')
      })
    }
  }
}
</script>