<template>
  <div class="message-alert">
    <div class="alert alert-dismissible" v-for="(item, i) in messages" :key="i" :class="'alert-' + item.status">
      {{ item.message }}
      <button type="button" class="close" @click="removeMessage(i)" aria-label="Close">
        <span aria-hidden="true">&times;</span>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Navbar',
  data() {
    return {
      messages: [],
    };
  },
  methods: {
    updateMessage(message, status) {
      const timestamp = Math.floor(new Date() / 1000); 
      //時間戳記，代表這筆訊息的身分證，可用於後續移除訊息時，找到指定的訊息
      
      this.messages.push({
        message,
        status,
        timestamp,
      });

      this.removeMessageWithTiming(timestamp);
    },
    removeMessage(num) {
      this.messages.splice(num, 1);
    },
    removeMessageWithTiming(timestamp) {
      const vm = this;

      setTimeout(() => {
        vm.messages.forEach((item, i) => {
          if (item.timestamp === timestamp) {
            vm.messages.splice(i, 1);
          }
        });
      }, 5000);

    },
  },
  created() {
    const vm = this;

    // 自定義名稱 'messsage:push'
    // message: 傳入參數
    // status: 樣式，預設值為 warning

    vm.$bus.$on('message:push', (message, status = 'warning') => { 
    vm.updateMessage(message, status);
    });

    //這一段表示要將這整個元件註冊到BUS工具箱內，呼叫該事件'message:push'，會做甚麼事情，而這裡需要兩個參數，message跟status
    //獲得這兩個參數時，會傳入updateMessage運作
    // vm.$bus.$emit('message:push')為其他元件呼叫該元件的起手式
    //當該元件被呼叫時，其HTML也會直接嵌入到網站上
  },
};
</script>

<style scope>
.message-alert {
  position: fixed;
  max-width: 50%;
  top: 56px;
  right: 20px;
  z-index: 1100;
}
</style>