<template>
  <div class="modal fade" id="addCouponModal"  data-backdrop="static" tabindex="-1" aria-hidden="true">
    <div class="modal-dialog modal-lg">

      <div class="modal-content border-0">

        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title">新增優惠券</h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span class="text-light" aria-hidden="true">&times;</span>
          </button>
        </div>

        <div class="modal-body">
          <div class="row">
            <div class="col-md-12">
              <div class="form-group">
                <label for="title">標題</label>
                <input type="text" id="title" class="form-control"  placeholder="請輸入標題" v-model="tempCoupon.title">
              </div>

              <div class="form-group">
                <label for="code">優惠碼</label>
                <input type="text" id="code" class="form-control" placeholder="請輸入優惠碼" v-model="tempCoupon.code">
              </div>

              <div class="form-group">
              <label for="due_date">到期日</label>
                <input type="date" class="form-control" id="due_date" placeholder="請輸入日期" v-model="due_date">
              </div>

              <div class="form-group">
              <label for="percent">折扣百分比</label>
                <input type="number" class="form-control" id="percent" placeholder="請輸入折扣百分比" v-model="tempCoupon.percent" 
                 onkeyup="value=value.replace(/[^\d]/g,'') "
                >
              </div>

              <div class="form-group">
                <div class="form-check">
                  <input id="is_enabled" class="form-check-input" type="checkbox" :true-value="1" :false-value="0" v-model="tempCoupon.is_enabled">
                <label class="form-check-label" for="is_enabled">是否啟用</label>
                </div>
              </div>

            </div>
          </div>
        </div>

        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">關閉</button>
          <button type="button" class="btn btn-dark" @click="submitData">確定</button>
        </div>

      </div>

    </div>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  data(){
    return{
      tempCoupon:{},
      due_date:new Date()
    }
  },
  props:{
    tempEdit:{
      type:Object,
      required:true
    },
    isNew:{
      type:Boolean,
      required:true
    }
  },
  methods:{
    submitData(){
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon`
      let httpMethods = 'post'

      if(!this.isNew){
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${this.tempCoupon.id}`
        httpMethods = 'put'
      }

      this.$http[httpMethods](api,{ data:this.tempCoupon }).then((response) => {
        if(response.data.success){
          this.$bus.$emit('message:push',response.data.message,'success');
        }
        else{
          this.$bus.$emit('message:push',response.data.message,'danger');
        }

        this.$emit('completeUpdate')
        $('#addCouponModal').modal('hide')
      })
    }
  },
  watch:{
    tempEdit:function(){
      this.tempCoupon = Object.assign({},this.tempEdit)
    },
    due_date:function(val){
      const timeStamp = Math.floor(new Date(val) / 1000)
      this.tempCoupon.due_date = timeStamp
    }
  }
}
</script>