<template>
  <div class="container-fluid px-0">

    <div>
      <loading :active.sync="isLoading_status"></loading>
    </div>

    <div class="my-4 mx-auto">

      <div class="text-center m-4">
        <h3>產品管理</h3>
      </div>

      <div class="my-4 mx-auto col-12 text-right">
        <button class="btn btn-dark" data-toggle="modal" data-target="#addProductModal" @click="newProduct">
          新增產品
        </button>
      </div>

      <div class="my-4 mx-auto col-12">
        <ProductsList :products_menu="products_menu" @changeHandler="editProduct"/>
      </div>

      <div class="my-4 mx-auto">
        <AddProduct :tempEdit="tempProduct" :isNew="isNew" @completeUpdate="completeUpdate"/>
        <DelProduct :tempProduct="tempProduct" @completeUpdate="completeUpdate"/>
      </div>

      <div class="my-4 mx-auto col-12">
        <Pagination :pagination_menu="pagination_menu" @pageChange="getProductList"/>
      </div>

    </div>

  </div>
</template>

<script>
import ProductsList from '@/components/Dashboard/Admin_products/Products_list'
import AddProduct from '@/components/Dashboard/Admin_products/Modal_addProduct'
import DelProduct from '@/components/Dashboard/Admin_products/Modal_delProduct'
import Pagination from '@/components/Pagination'

export default {
  data(){
    return{
      tempProduct:{},
      isNew:true
    }
  },
  components:{
    ProductsList,
    Pagination,
    AddProduct,
    DelProduct
  },
  methods:{
    getProductList(page = 1){
      this.$store.dispatch('init_products_list',page)
    },
    newProduct(){
      this.isNew = true
      this.tempProduct = {}
    },
    editProduct(val){
      this.isNew = false
      this.tempProduct = val
    },
    completeUpdate(){
      this.tempProduct = {}
      this.getProductList(this.pagination_menu.current_page)
    },
    loadingHandler(){
      this.isLoading = !this.isLoading
    }
  },
  created(){
    this.getProductList()
  },
  computed:{
    products_menu(){
      return this.$store.getters['PRODUCTS_LIST_MENU']
    },
    pagination_menu(){
      return this.$store.getters['PRODUCTS_PAGINATION_MENU']
    },
    isLoading_status(){
      return this.$store.getters['LOADING_STATUS']
    }
  }
}
</script>