import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home'
import Main from '../views/Home_view/Main'
import Store from '../views/Home_view/Store'
import OrderCheck from '../views/Home_view/OrderCheck'

import Login from '../views/Login'

import Dashboard from '../views/Dashboard'
import Admin_Products from '../views/Dashboard_view/Admin_products'
import Admin_Coupon from '../views/Dashboard_view/Admin_coupon'
import Admin_Orderlist from '../views/Dashboard_view/Admin_orderlist'
import Admin_Store from '../views/Dashboard_view/Admin_store'
import Admin_Ordercheck from '../views/Dashboard_view/Admin_ordercheck'

Vue.use(VueRouter)

const routes = [
  {
    path:'*',
    redirect:'/'
  },
  {
    path: '/',
    component: Home,
    children:[
      {
        path:'/',
        name:'main',
        component: Main
      },
      {
        path:'store',
        name: 'Store',
        component: Store
      },
      {
        path:'order_check/:orderId',
        name: 'OrderCheck',
        component: OrderCheck
      }
    ]
  },
  {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/admin',
    name: 'admin',
    component: Dashboard,
    redirect:'admin/products',
    meta: { requiresAuth: true },
    children:[
      {
        path: 'products',
        name: 'AdminProducts',
        component: Admin_Products,
        meta: { requiresAuth: true }
      },
      {
        path: 'coupon',
        name: 'AdminCoupon',
        component: Admin_Coupon,
        meta: { requiresAuth: true }
      },
      {
        path: 'orderlist',
        name: 'AdminOrderList',
        component: Admin_Orderlist,
        meta: { requiresAuth: true }
      },
      {
        path: 'store',
        name: 'AdminStore',
        component: Admin_Store,
        meta: { requiresAuth: true }
      },
      {
        path: 'order_check/:orderId',
        name: 'AdminOrderCheck',
        component: Admin_Ordercheck,
        meta: { requiresAuth: true }
      }
    ]
  }
]

const router = new VueRouter({
  routes,
  linkActiveClass: 'active'
})

export default router
