<template>
  <nav class="navbar navbar-expand-md navbar-light bg-light fixed-top sticky-top">
    <div class="container-fluid">
          <span class="navbar-brand">Meida Station</span>

    <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#collapseNavbar">
      <span class="navbar-toggler-icon"></span>
    </button>

    <div class="collapse navbar-collapse" id="collapseNavbar">
      <ul class="navbar-nav mr-auto">
        <li class="nav-item link-Style" @click="closeNavbar">
          <router-link to="/" class="nav-link" exact>Home</router-link>
        </li>
        <li class="nav-item link-Style" @click="closeNavbar">
          <router-link to="/store" class="nav-link" exact>Products</router-link>
        </li>
      </ul>
      
      <form class="userbar form-inline my-3 my-lg-0">

        <router-link class="mx-2" to="/login">
          <i class="text-muted icon far fa-user-circle"></i>
        </router-link>

        <a class="mx-2" @click="openCartModal">
           <i class="icon text-muted fas fa-shopping-cart">
             <span class="counter-circle" v-if="cart_counter>0">
               {{cart_counter}}
              </span>
           </i>
        </a>

      </form>
    </div>
    </div>
  </nav>
</template>

<script>
import $ from 'jquery'
export default {
  data(){
    return{}
  },
  methods:{
    openCartModal(){
      $('#customerCartModal').modal('show')
    },
    closeNavbar(){
      $('#collapseNavbar').collapse('hide')
    }
  },
  computed:{
    cart_counter(){
      let cart_data = this.$store.getters['CART_MENU']

      return cart_data.carts.length 
    }
  }
}
</script>

<style lang="scss" scoped>


.navbar-brand{
  margin-right:2rem;
  font-weight: 700;
}

.nav-link{
  font-weight: 700;
  text-align: center;
  margin-right: 1rem;
  border-bottom: .2rem solid transparent;
  outline: none;
}

.nav-link:hover{
  border-bottom: .2rem solid transparent;
  border-bottom-color: rgb(187, 56, 56);
  transition: ease-in-out .4s;
}

.active{
  border-bottom: .2rem solid transparent;
  border-bottom-color: rgb(187, 56, 56);
}

.userbar{
  a{
    outline: none;
    opacity: .8;
  }
  .icon{
    font-size: 25px;
    font-weight: 600;
    position: relative;
    cursor: pointer;
  }
  .counter-circle{
    position:absolute;
    width: 20px;
    height: 20px;
    top:-1vh;
    left: -1vh;
    bottom: 0;
    background-color: red;
    color: white;
    font-size: 14px;
    font-weight: 800;
    text-align: center;
    line-height: 20px;
    border-radius: 50%;
  }
}

</style>