export default function (str) {
    let Arraystr = str.split('')
    let temp = [];
    let newStr;


      for(let i=0;i<45;i++){
        temp.push(Arraystr[i]);
      }

      newStr = temp.join('');

      if(Arraystr.length >45){
        newStr += ' ‧‧‧'
      }

      return newStr;
  }