<template>
  <div>
    <table class="table rwdTable">
      <thead>
        <th width="120">分類</th>
        <th>商品名稱</th>
        <th width="120">原價</th>
        <th width="120">售價</th>
        <th width="100">啟用狀態</th>
        <th width="120">編輯</th>
      </thead>
      <tbody>
        <tr v-for="(item) in products_menu" :key="item.id">
          <td scope="col" class="rwdTitle" data-title="分類">{{item.category}}</td>
          <td scope="col" class="rwdTitle" data-title="商品名稱">{{item.title}}</td>
          <td scope="col" class="rwdTitle" data-title="原價">{{item.origin_price | currency}}</td>
          <td scope="col" class="rwdTitle" data-title="售價">{{item.price | currency}}</td>
          <td scope="col" class="rwdTitle" data-title="啟用狀態">
            <span v-if="item.is_enabled" class="text-success">啟用</span>
            <span v-else>未啟用</span>
          </td>
          <td scope="col" class="rwdTitle" data-title="編輯">
            <div class="btn-group">
              <button class="btn btn-outline-primary btn-sm" @click="editHandler(item)">編輯</button>
              <button class="btn btn-outline-danger btn-sm" @click="delHandler(item)">刪除</button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  data(){
    return{}
  },
  props:{
    products_menu:{
      type:Array,
      required:true
    }
  },
  methods:{
    editHandler(data){
      $('#addProductModal').modal('show')
      this.$emit('changeHandler',data)
    },
    delHandler(data){
      $('#delProductModal').modal('show')
      this.$emit('changeHandler',data)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/helper/_rwdTable";
</style>