<template>
  <div class="modal fade" id="infoProductModal"  tabindex="-1" aria-hidden="true">
    <div class="modal-dialog">

      <div class="modal-content border-0">

        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title">{{productInfo_menu.title}}</h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span class="text-light" aria-hidden="true">&times;</span>
          </button>
        </div>

        <div class="modal-body">
          <img :src="productInfo_menu.imageUrl" class="productImg">
          <blockquote class="blockquote mt-3">
            <p>{{productInfo_menu.content}}</p>
            <footer class="blockquote-footer text-right">{{productInfo_menu.description}}</footer>
          </blockquote>
          <div class="d-flex justify-content-between align-items-baseline">
            <div class="h4" v-if="!productInfo_menu.price">{{productInfo_menu.origin_price | currency}} 元</div>
            <del class="h6" v-if="productInfo_menu.price">原價 {{productInfo_menu.origin_price * productNum | currency}} 元</del>
            <div class="h4" v-if="productInfo_menu.price">特價 {{productInfo_menu.price * productNum | currency}} 元</div>
          </div>
          <select name="" class="form-control mt-3" v-model="productNum">
            <option :value="num" v-for="num in 10" :key="num">
              選購 {{num}} {{productInfo_menu.unit}}
            </option>
          </select>
        </div>

        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">關閉</button>
          <button type="button" class="btn btn-dark" @click="addToCart(productInfo_menu.id,productNum)">確定</button>
        </div>

      </div>

    </div>
  </div>
</template>

<script>
export default {
  data(){
    return{
      productNum:1
    }
  },
  props:{
    productInfo_menu:{
      type:Object,
      require:true
    }
  },
  methods:{
    addToCart(id,qty){
      this.$emit('addToCartHandler',id,qty)
      window.setTimeout(()=>{this.productNum = 1},1000)
    }
  }
}
</script>

<style lang="scss" scoped>
.productImg{
   max-width: 100%;
   height: 300px;
   display: block;
   margin: 0px auto;
}

.modal-body{
  p{
    font-size: 15px;
  }
}
</style>