<template>
  <nav id="sidebarMenu" class="col-md-3 col-lg-2 d-md-block bg-light sidebar collapse">
    <div class="sidebar-sticky pt-3">
      <ul class="nav flex-column">

        <li class="nav-item" @click="closeModal">
          <router-link to="/admin/products" class="nav-link">
            <i class="icon fas fa-box-open"></i>
            產品管理
          </router-link>
        </li>
        <li class="nav-item" @click="closeModal">
          <router-link to="/admin/orderlist" class="nav-link">
            <i class="icon far fa-list-alt"></i>
            訂單管理 
          </router-link>
        </li>
        <li class="nav-item" @click="closeModal">
          <router-link to="/admin/coupon" class="nav-link">
            <i class="icon fas fa-ticket-alt"></i>
            優惠券管理
          </router-link>
        </li>
        <li class="nav-item" @click="closeModal">
          <router-link to="/admin/store" class="nav-link">
            <i class="icon fas fa-shopping-cart"></i>
            模擬訂單
          </router-link>
        </li>
        <li class="nav-item">
          <a class="signout-btn nav-link" @click="signout">
            <i class="icon fas fa-sign-in-alt"></i>
            登出
          </a>
        </li>
      </ul>
    </div>
  </nav>
</template>

<script>
import $ from 'jquery'
export default {
  data(){
    return{}
  },
  methods:{
    signout(){
      const api = `${process.env.VUE_APP_APIPATH}/logout`;

      this.$http.post(api).then((response) => {
        if(response.data.success){
          this.$bus.$emit('message:push',response.data.message,'success');
          this.$router.push('/');
        }
        else{
          this.$bus.$emit('message:push',response.data.message,'danger');
        }
      })
    },
    closeModal(){
      $('#sidebarMenu').collapse('hide')
    }
  }
}
</script>>

<style lang="scss" scoped>
 .sidebar {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    z-index: 100; 
    padding: 48px 0 0;
    box-shadow: inset -1px 0 0 rgba(0, 0, 0, .1);
  }
  
  .sidebar-sticky {
    position: relative;
    top: 0;
    height: calc(100vh - 48px);
    padding-top: .5rem;
    overflow-x: hidden;
    overflow-y: auto;
  }
  
  @supports ((position: -webkit-sticky) or (position: sticky)) {
    .sidebar-sticky {
      position: -webkit-sticky;
      position: sticky;
    }
  }
  
  .sidebar .nav-link {
    font-weight: 500;
    color: #333;
  }
  
  .sidebar .nav-link .feather {
    margin-right: 4px;
    color: #999;
  }
  
  .sidebar .nav-link.active {
    color: #e2e5e7;
  }
  
  .sidebar .nav-link:hover .feather,
  .sidebar .nav-link.active .feather {
    color: inherit;
  }

  .icon{
    margin-right: 10px;;
  }
  
  .signout-btn{
    cursor: pointer;
  }

  .nav-item:hover{
    background-color: rgb(206, 209, 216);;
  }

  .active{
    background-color: rgb(59, 59, 59);
  }
</style>