<template>
  <div class="row">
    <div class="col-12 col-md-6 col-xl-5 my-3 mx-auto" v-for="(item) in products_menu" :key="item.id">
      <div class="card border-0 shadow-sm">
        <div class="product-img" :style="{backgroundImage: `url(${item.imageUrl})`}">
        </div>

        <div class="card-body">
          <span class="badge badge-secondary float-right ml-2">{{item.category}}</span>
          <h3 class="card-title">
            <span class="text-dark">{{item.title}}</span>
          </h3>
          <p class="card-text">{{item.content | textFilter}}</p>
          <div class="d-flex justify-content-between align-items-baseline">
            <del class="h6">{{item.origin_price | currency}}</del>
            <div class="h3 text-danger">{{item.price | currency}}</div>
          </div>
        </div>

        <div class="card-footer bg-dark d-flex">
          <button v-if="item.is_enabled" type="button" class="btn-font-size btn btn-info btn-md" @click="openInfoModal(item)">
            <i class="fas fa-spinner fa-spin" v-if="status.infoLoading === item.id"></i>
            查看更多
          </button>
          <button v-else type="button" class="btn-font-size btn btn-light text-secondary btn-md">
            未啟用
          </button>
          <button type="button" class="btn-font-size btn btn-danger btn-md ml-auto" @click="addToCart(item.id)">
            <i class="fas fa-spinner fa-spin" v-if="status.buyLoading === item.id"></i>
            放入購物車
          </button>
        </div>

        </div>
      </div>
    </div>

</template>

<script>
export default {
  data(){
    return{}
  },
  props:{
    products_menu:{
      type:Array,
      required:true
    },
    status:{
      type:Object,
      required:true
    }
  },
  methods:{
    openInfoModal(item){
      if(item.is_enabled){
        this.$emit('infoHandler',item.id)
      }
    },
    addToCart(id){
      this.$emit('addToCartHandler',id)
    }
  }
}
</script>

<style lang="scss" scoped>
.product-img{
  height: 300px;
  background-size: cover;
  background-position: center;
}

.card{
  height: 550px;
}

.card-text{
  font-size: 15px;
}

@media only screen and (max-width:300px){
  .btn-font-size{
    font-size: 12px;
  }
}
</style>