<template>
  <div class="container-fluid px-0">

    <div>
      <loading :active.sync="isLoading_status"></loading>
    </div>

    <div class="my-4 mx-auto">

      <div class="text-center m-4">
        <h3>優惠券管理</h3>
      </div>

      <div class="my-4 mx-auto col-12 text-right">
        <button class="btn btn-dark" data-toggle="modal" data-target="#addCouponModal" @click="newCoupon">
          建立新的優惠券
        </button>
      </div>

      <div class="my-4 mx-auto col-12">
        <CouponsList :coupons_menu="coupons_menu" @changeHandler="editCoupon"/>
      </div>

      <div class="my-4 mx-auto">
        <AddCoupon :tempEdit="tempCoupon" :isNew="isNew" @completeUpdate="completeUpdate"/>
        <DelCoupon :tempCoupon="tempCoupon" @completeUpdate="completeUpdate"/>
      </div>

      <div class="my-4 mx-auto col-12">
        <Pagination :pagination_menu="pagination_menu" @pageChange="getCouponList"/>
      </div>

    </div>
    
  </div>
</template>

<script>
import CouponsList from '@/components/Dashboard/Admin_coupon/Coupon_list'
import AddCoupon from '@/components/Dashboard/Admin_coupon/Modal_addCoupon'
import DelCoupon from '@/components/Dashboard/Admin_coupon/Modal_delCoupon'
import Pagination from '@/components/Pagination'

export default {
  data(){
    return{
      tempCoupon:{},
      isNew:true
    }
  },
  components:{
    CouponsList,
    AddCoupon,
    DelCoupon,
    Pagination
  },
  methods:{
    getCouponList(page = 1){
      this.$store.dispatch('init_coupons_list',page)
    },
    newCoupon(){
      this.tempCoupon = {}
      this.isNew = true
    },
    editCoupon(val){
      this.tempCoupon = val
      this.isNew = false
    },
    completeUpdate(){
      this.tempCoupon = {}
      this.getCouponList(this.pagination_menu.current_page)
    }
  },
  created(){
    this.getCouponList()
  },
  computed:{
    coupons_menu(){
      return this.$store.getters['COUPONS_LIST_MENU']
    },
    pagination_menu(){
      return this.$store.getters['COUPONS_PAGINATION_MENU']
    },
    isLoading_status(){
      return this.$store.getters['LOADING_STATUS']
    }
  }
}
</script>