<template>
  <div class="modal fade" id="addProductModal"  data-backdrop="static" tabindex="-1" aria-hidden="true">
    <div class="modal-dialog modal-lg">

      <div class="modal-content border-0">

        <div class="modal-header bg-dark text-white">
          <h5 class="modal-title">新增產品</h5>
          <button type="button" class="close" data-dismiss="modal" aria-label="Close">
            <span class="text-light" aria-hidden="true">&times;</span>
          </button>
        </div>

        <div class="modal-body">
          <div class="row">

            <div class="col-12 col-lg-4 col-xl-4">
              <div class="form-group">
                <label for="image">輸入圖片網址</label>
                <input type="text" class="form-control" id="image" placeholder="請輸入圖片連結" v-model="tempProduct.imageUrl">
              </div>
              <div class="form-group">
                <label for="customFile">或 上傳圖片
                  <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                </label>
                <input type="file" id="customFile" class="form-control" ref="files" @change="uploadImg">
              </div>
              <img class="img-fluid" img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
               alt="" :src="tempProduct.imageUrl">
            </div>

            <div class="col-12 col-lg-8 col-xl-8">
              <div class="form-group">
                <label for="title">標題</label>
                <input id="title" type="text" class="form-control" placeholder="請輸入標題" v-model="tempProduct.title">
              </div>
              <div class="form-row">
                <div class="form-group col-md-6">
                  <label for="category">分類</label>
                  <input id="category" type="text" class="form-control" placeholder="請輸入分類" v-model="tempProduct.category">
                </div>
                <div class="form-group col-md-6">
                  <label for="price">單位</label>
                  <input id="unit" type="unit" class="form-control" placeholder="請輸入單位" v-model="tempProduct.unit">
                </div>
              </div>
              <div class="form-row">
                <div class="form-group col-md-6">
                  <label for="origin_price">原價</label>
                  <input id="origin_price" type="number" class="form-control" placeholder="請輸入原價" v-model="tempProduct.origin_price">
                </div>
                <div class="form-group col-md-6">
                  <label for="price">售價</label>
                  <input id="price" type="number" class="form-control" placeholder="請輸入售價" v-model="tempProduct.price">
                </div>
              </div>
              
              <hr>

              <div class="form-group">
                <label for="description">商品描述</label>
                <textarea id="description" type="text" class="form-control" placeholder="請輸入商品描述" v-model="tempProduct.description"></textarea>
              </div>
              <div class="form-group">
                <label for="content">商品內容</label>
                <textarea id="content" type="text" class="form-control" placeholder="請輸入商品內容" v-model="tempProduct.content"></textarea>
              </div>
              <div class="form-group">
                <div class="form-check">
                  <input id="is_enabled" class="form-check-input" type="checkbox" :true-value="1" :false-value="0" v-model="tempProduct.is_enabled">
                  <label class="form-check-label" for="is_enabled">是否啟用</label>
                </div>
              </div>

            </div>
          </div>
        </div>

        <div class="modal-footer">
          <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">關閉</button>
          <button type="button" class="btn btn-dark" @click="submitData">確定</button>
        </div>

      </div>

    </div>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  data(){
    return{
      tempProduct:{},
      status:{
        fileUploading:false
      }
    }
  },
  props:{
    tempEdit:{
      type:Object,
      required:true
    },
    isNew:{
      type:Boolean,
      required:true
    }
  },
  methods:{
    uploadImg(){
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/upload`;
      const uplaodFile = this.$refs.files.files[0];
      const formData = new FormData();

      formData.append('file-to-upload',uplaodFile) //選擇要進行的事情，以及要上傳的檔案

      this.status.fileUploading = true; //上傳之前，先進入Loading遮罩

      this.$http.post(api,formData,{
        headers:{
          'Content-Type' : 'multipart/form-data' //後端指定使用的標頭
        }
      }).then((response) => {
        if(response.data.success){
          this.status.fileUploading = false; //從後端收到上傳成功的消息時，會關閉Loading遮罩
          this.$set(this.tempProduct,'imageUrl',response.data.imageUrl)
        }
        else{
          this.$bus.$emit('message:push',response.data.message,'danger');
        }
      })
    },
    submitData(){
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product`
      let httpMethods = 'post'

      if(!this.isNew){
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${this.tempProduct.id}`
        httpMethods = 'put'
      }

      this.$http[httpMethods](api, { data: this.tempProduct }).then((response) => {
        if(response.data.success){
          this.$bus.$emit('message:push',response.data.message,'success');
        }
        else{
          this.$bus.$emit('message:push',response.data.message,'danger');
        }

        this.$emit('completeUpdate')
        
        $('#addProductModal').modal('hide')
      })

      
    }
  },
  watch:{
    tempEdit:function(){
      this.tempProduct = Object.assign({},this.tempEdit);
    }
  }
}
</script>