<template>
  <div class="container">
    <form class="form-signin">
      <i class="icon far fa-user"></i>

      <h3 class="title">後台系統登入</h3>

      <label for="inputEmail" class="sr-only">電子信箱</label>
      <input v-model="user.username" type="email" id="inputEmail" class="form-control" placeholder="Email address" required autofocus>

      <label for="inputPassword" class="sr-only">密碼</label>
      <input v-model="user.password" type="password" id="inputPassword" class="form-control" placeholder="Password" required>

      <button type="button" class="btn btn-lg btn-dark btn-block" @click="login">登入</button>

      <p class="copyright text-muted">&copy; 2017-2020</p>
    </form>
  </div>
</template>

<script>
export default {
  data(){
    return{
      user:{
        username:'',
        password:''
      },
      isLoading:false
    }
  },
  methods:{
    login(){
      const api = `${process.env.VUE_APP_APIPATH}/admin/signin`;

      this.$http.post(api,this.user).then((response) => {
        if(response.data.success){
          this.$bus.$emit('message:push',response.data.message,'success');
          this.$router.push('/admin/products');
        }
        else{
           this.$bus.$emit('message:push',response.data.message,'danger');
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.icon{
  display: block;
  background-color: rgb(165, 205, 238);
  color: white;
  text-align: center;
  font-size: 180px;
  margin: 10px auto;
  padding: 50px 0px;
  border-radius: 50%;
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
  .title{
    text-align: center;
    margin: 20px auto;
  }
  .copyright{
    margin: 10px auto;
  }
}

.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
</style>