<template>
  <div class="container-fluid px-0">
    
    <div>
      <loading :active.sync="isLoading_status"></loading>
    </div>
    
    <div class="my-4 mx-auto">

      <div class="text-center m-4">
        <h3>訂單管理</h3>
      </div>

      <div class="my-4 mx-auto col-12">
        <table class="table rwdTable">
          <thead>
            <th width="40">購買時間</th>
            <th width="100">E-Mail</th>
            <th width="140">購買款項</th>
            <th width="40">應付金額</th>
            <th width="60">是否付款</th>
          </thead>
          <tbody>
            <tr v-for="(item) in orders_menu" :key="item.id">
              <td class="rwdTitle" data-title="購買時間">{{item.create_at | timeChanger}}</td>
              <td class="rwdTitle" data-title="E-Mail">{{item.user.email}}</td>
              <td class="rwdTitle" data-title="購買款項">
                <div v-for="(product,index) in item.products" :key="item.id+product.product_id+index">
                  {{product.id}} 數量 : {{product.qty}}
                </div>
              </td>
              <td class="text-right rwdTitle" data-title="應付金額">{{item.total}}</td>
              <td class="rwdTitle" data-title="是否付款">
                <span v-if="item.is_paid" class="text-success">已付款</span>
                <span class="text-danger" v-else>尚未付款</span>
              </td>
            </tr>
          </tbody>
        </table>
      </div>

      <div class="my-4 mx-auto">
        <Pagination :pagination_menu="pagination_menu" @pageChange="getOrdersList"/>
      </div>

    </div>

  </div>
</template>

<script>
import Pagination from '@/components/Pagination'
export default {
  data(){
    return{}
  },
  components:{
    Pagination
  },
  methods:{
    getOrdersList(page = 1){
      this.$store.dispatch('init_orders_list',page)
    }
  },
  created(){
    this.getOrdersList()
  },
  computed:{
    orders_menu(){
      return this.$store.getters['ORDERS_LIST_MENU']
    },
    pagination_menu(){
      return this.$store.getters['ORDERS_PAGINATION_MENU']
    },
    isLoading_status(){
      return this.$store.getters['LOADING_STATUS']
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/helper/_rwdTable";
</style>