import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  strict:true,
  state: {
    productsList:{
      products:[],
      pagination:{}
    },
    couponsList:{
      coupons:[],
      pagination:{}
    },
    ordersList:{
      orders:[],
      pagination:{}
    },
    storeList:{
      products:[],
      pagination:{}
    },
    productInfo:{},
    cartData:{
      carts:[]
    },
    isLoading:false
  },
  getters:{
    PRODUCTS_LIST_MENU(state){
      return state.productsList.products
    },
    PRODUCTS_PAGINATION_MENU(state){
      return state.productsList.pagination
    },
    COUPONS_LIST_MENU(state){
      return state.couponsList.coupons
    },
    COUPONS_PAGINATION_MENU(state){
      return state.couponsList.pagination
    },
    ORDERS_LIST_MENU(state){
      return state.ordersList.orders
    },
    ORDERS_PAGINATION_MENU(state){
      return state.ordersList.pagination
    },
    STORE_LIST_MENU(state){
      return state.storeList.products
    },
    STORE_PAGINATION_MENU(state){
      return state.storeList.pagination
    },
    PRODUCT_INFO_MENU(state){
      return state.productInfo
    },
    CART_MENU(state){
      return state.cartData
    },
    LOADING_STATUS(state){
      return state.isLoading
    }
  },
  mutations: {
    get_products_list(state,data){
      let temp

      temp = JSON.stringify(data.products)
      state.productsList.products = JSON.parse(temp)

      temp = JSON.stringify(data.pagination)
      state.productsList.pagination = JSON.parse(temp)
    },
    get_coupons_list(state,data){
      let temp

      temp = JSON.stringify(data.coupons)
      state.couponsList.coupons = JSON.parse(temp)

      temp = JSON.stringify(data.pagination)
      state.couponsList.pagination = JSON.parse(temp)
    },
    get_orders_list(state,data){
      let temp

      temp = JSON.stringify(data.orders)
      state.ordersList.orders = JSON.parse(temp)

      temp = JSON.stringify(data.pagination)
      state.ordersList.pagination = JSON.parse(temp)
    },
    get_store_list(state,data){
      let temp

      temp = JSON.stringify(data.products)
      state.storeList.products = JSON.parse(temp)

      temp = JSON.stringify(data.pagination)
      state.storeList.pagination = JSON.parse(temp)
    },
    get_product_info(state,data){
      let temp

      temp = JSON.stringify(data)
      state.productInfo = JSON.parse(temp)
    },
    get_cart_data(state,data){
      let temp

      temp = JSON.stringify(data)
      state.cartData = JSON.parse(temp)
    },
    loading_handler(state,status){
      state.isLoading = status
    }
  },
  actions: {
    init_products_list({commit},page){
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/products?page=${page}`;

      commit('loading_handler',true)

      axios.get(api).then((response) => {
        commit('loading_handler',false)

        if(response.data.success){
          commit('get_products_list',response.data)
        }
        else{
          alert('取得商品列表失敗')
        }
      })
      .catch((error) =>{
        alert(error.message)
      })
    },
    init_coupons_list({commit},page){
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupons?page=${page}`;

      commit('loading_handler',true)

      axios.get(api).then((response) => {
        commit('loading_handler',false)
        if(response.data.success){
          commit('get_coupons_list',response.data)
        }
        else{
          alert('取得優惠券列表失誤')
        }
      })
      .catch((error) =>{
        alert(error.message)
      })
    },
    init_orders_list({commit},page){
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/orders?page=${page}`

      commit('loading_handler',true)

      axios.get(api).then((response) => {
        commit('loading_handler',false)
        if(response.data.success){
          commit('get_orders_list',response.data)
        }
        else{
          alert('取得訂單資料失敗')
        }
      })
      .catch((error) =>{
        alert(error.message)
      })
    },
    init_store_list({commit},page){
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products?page=${page}`

      commit('loading_handler',true)

      axios.get(api).then((response) => {
        commit('loading_handler',false)
        if(response.data.success){
          commit('get_store_list',response.data)
        }
        else{
          alert('取得商店資料失敗')
        }
      })
      .catch((error)=>{
        alert(error.message)
      })
    },
    init_product_info({commit},id){
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/product/${id}`;
      
      axios.get(api).then((response) => {
        if(response.data.success){
          commit('get_product_info',response.data.product)
        }
        else{
          alert('取得商品詳細資料錯誤')
        }
      })
      .catch((error)=>{
        alert(error.message)
      })
    },
    init_cart_data({commit}){
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`;

      commit('loading_handler',true)

      axios.get(api).then((response) => {
        commit('loading_handler',false)
        if(response.data.success){
          commit('get_cart_data',response.data.data)
        }
        else{
          alert('取得購物車資料失敗')
        }
      })
      .catch((error)=>{
        alert(error.message)
      })
    }
  },
  modules: {
  }
})
