<template>
  <div class="contaier-fluid">

    <div class="my-4 mx-auto">

      <div class="col-8 my-6 mx-auto p-6 text-center alert alert-danger">
        此為付款測試頁面，<strong>不會進行任何金流活動</strong>，僅會送出一個假資料進行驗證與測試
      </div>

      <div class="my-4 mx-auto col-12 col-md-8">

        <div class="my-4 buyList">
          <table class="table overFlowTable">
            <thead>
              <th class="text-danger" width="300">品名</th>
              <th class="text-danger" width="200">數量</th>
              <th class="text-danger" width="100">單價</th>
            </thead>
            <tbody>
              <tr v-for="item in orderData.order.products" :key= item.id >
                <td>{{item.product.title}}</td>
                <td>{{item.qty}}/{{item.product.unit}}</td>
                <td class="text-right">{{item.final_total}}</td>
              </tr>
              <tr>
                <td colspan="2" class="text-right"><h5>總計</h5></td>
                <td  class="text-right"><h5>{{orderData.order.total}}</h5></td>
              </tr>
            </tbody>
          </table>
        </div>

        <div class="my-4 infoList">
          <table class="table overFlowTable">
            <tbody>
              <tr>
                <td width="120"><b>Email</b></td>
                <td>{{orderData.order.user.email}}</td>
              </tr>
              <tr>
                <td width="120"><b>姓名</b></td>
                <td>{{orderData.order.user.name}}</td>
              </tr>
              <tr>
                <td width="120"><b>收件人電話</b></td>
                <td>{{orderData.order.user.tel}}</td>
              </tr>
              <tr>
                <td width="120"><b>收件人地址</b></td>
                <td>{{orderData.order.user.address}}</td>
              </tr>
              <tr>
                <td width="120"><b>付款狀態</b></td>
                <td v-if="orderData.order.is_paid" class="text-success"><strong>付款完成</strong></td>
                <td v-else class="text-danger">尚未付款</td>
              </tr>
            </tbody>
          </table>
        </div>

        <div class="my-4 mx-auto text-right" v-if="orderData.order.is_paid === false">
          <button type="submit" class="btn btn-danger" @click.prevent="payIt">確認付款(僅功能測試)</button>
        </div>

      </div>

    </div>
    
  </div>
</template>

<script>
export default {
  data(){
    return{
      orderId:'',
      orderData:{
        order:{
          user:{}
        }
      }
    }
  },
  methods:{
    getOrderCheck(){
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order/${this.orderId}`
      
      this.$http.get(api).then((response)=>{
        if(response.data.success){
          this.orderData = response.data
        }
        else{
          this.$bus.$emit('message:push',response.data.message,'danger');
        }
      })
    },
    payIt(){
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/pay/${this.orderId}`

      this.$http.post(api).then((response)=>{
        if(response.data.success){
          this.$bus.$emit('message:push',response.data.message,'success');
          this.getOrderCheck()
        }
        else{
          this.$bus.$emit('message:push',response.data.message,'danger');
        }
      })
    }
  },
  created(){
    this.orderId = this.$route.params.orderId
    this.getOrderCheck()
  }
}
</script>

<style lang="scss" scoped>
.buyList{
  max-width: 100%;
  overflow:auto;
}

.infoList{
  max-width: 100%;
  overflow:auto;
}

@media only screen and (max-width:300px){
  .overFlowTable{
    width: 300px;
  }
}
</style>